///<reference path="./fixes.d.ts" />

import API from "./api";
import langSchema from '/lang.json!json'

class Lang {

    private langSchema:any = null;

    constructor() {
        this.langSchema = langSchema;
    }

    public get (key:string) : string {
        let schema = this.langSchema;
        let path = key.split('.');
        let currentKey = path.shift();
        do {
            if (this.isKeyExist(schema, currentKey, key)) {
                schema = schema[currentKey];
                currentKey = path.shift();
            }
        } while (path.length > 0);

        if (this.isKeyExist(schema, currentKey, key)) {
            return schema[currentKey];
        }

        return null;
    }

    private isKeyExist (schema:any, key:string, fullPath:string) : boolean {
        if (typeof schema[key] === "undefined") {
            throw "Can't find '" + key + "' key. Full path: " + fullPath;
        }

        return true;
    }

}

const lang:Lang = new Lang();

export default lang;
